import { useEffect, useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate } from 'react-router'

import {
  Box,
  Button,
  Link,
  TextField as MuiTextField,
  Paper,
  type TextFieldProps,
  Typography,
  styled,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { useLogin } from 'src/hooks/api/authHooks'

const PageWrapper = styled(Box)(
  () => `
      display: flex;
      justify-content: center;
      align-items: center;
  `
)

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

function Login() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()

  const user = useAuthUser()()

  const [values, setValues] = useState({
    email: null,
    password: null
  })

  const loginMutation = useLogin(values)

  useEffect(() => {
    if (user) {
      navigate('/')
    }
  }, [user])

  const handleChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    const newValues = {
      ...values,
      [event.target.name]: event.target.value
    }
    setValues(newValues)
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = e => {
    e.preventDefault()
    loginMutation.mutate()
  }

  return (
    <PageWrapper>
      <Helmet>
        <title>로그인</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(13),
          padding: 3,
          flex: 1,
          maxWidth: '360px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          },
          height: { xs: '90vh', sm: 'inherit' }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          KLUB에 로그인
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <TextField id="email" name="email" type="email" label="(학교) 이메일" onChange={handleChange} />
          <TextField id="password" name="password" type="password" label="비밀번호" onChange={handleChange} />
          <Box sx={{ width: '100%', pr: 1 }}>
            <Link href="/auth/find-password" sx={{ mt: 1 }}>
              <Typography variant="subtitle1" sx={{ textAlign: 'right' }}>
                비밀번호 찾기
              </Typography>
            </Link>
          </Box>

          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            로그인
          </Button>
          <Link href="/auth/register" sx={{ mt: 1 }}>
            아직 계정이 없으신가요?
          </Link>
        </Box>
      </Paper>
    </PageWrapper>
  )
}

export default Login
